'use client'

import { useState, useRef, useEffect } from 'react'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { ScrollArea } from '@/components/ui/scroll-area'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'

interface Message {
  id: string
  role: 'user' | 'assistant'
  content: string
  timestamp: Date
}

export default function ChatBot() {
  const [messages, setMessages] = useState<Message[]>([
    {
      id: '1',
      role: 'assistant',
      content: 'Hello! I\'m your AI squat coach. I can help you with:\n\n• Proper squat form and technique\n• Common mistakes to avoid\n• Personalized training tips\n• Injury prevention\n• Progressive overload strategies\n\nWhat would you like to know about squats?',
      timestamp: new Date()
    }
  ])
  const [input, setInput] = useState('')
  const [isLoading, setIsLoading] = useState(false)
  const scrollAreaRef = useRef<HTMLDivElement>(null)

  const scrollToBottom = () => {
    if (scrollAreaRef.current) {
      const scrollContainer = scrollAreaRef.current.querySelector('[data-radix-scroll-area-viewport]')
      if (scrollContainer) {
        scrollContainer.scrollTop = scrollContainer.scrollHeight
      }
    }
  }

  useEffect(() => {
    scrollToBottom()
  }, [messages])

  const generateAIResponse = async (userMessage: string): Promise<string> => {
    // Simulate AI response based on keywords
    const lowerMessage = userMessage.toLowerCase()
    
    if (lowerMessage.includes('depth') || lowerMessage.includes('how deep')) {
      return 'For proper squat depth, aim to lower your hips until your hip crease is just below your knee cap. This ensures full range of motion and maximum muscle activation. Start with bodyweight squats and gradually increase depth as your mobility improves.'
    }
    
    if (lowerMessage.includes('knee') || lowerMessage.includes('knees')) {
      return 'Keep your knees tracking in line with your toes throughout the movement. Avoid letting them cave inward (knee valgus) as this can lead to injury. Focus on pushing your knees out slightly and maintaining this alignment from start to finish.'
    }
    
    if (lowerMessage.includes('back') || lowerMessage.includes('spine')) {
      return 'Maintain a neutral spine throughout the squat. Keep your chest up, shoulders back, and core engaged. Avoid excessive forward lean or rounding of the back. Think about keeping your torso as upright as possible while still allowing for natural hip hinge movement.'
    }
    
    if (lowerMessage.includes('feet') || lowerMessage.includes('stance')) {
      return 'Position your feet shoulder-width apart with toes pointed slightly outward (15-30 degrees). This stance allows for optimal hip mobility and knee tracking. Distribute weight evenly across your feet, focusing on the mid-foot and heel.'
    }
    
    if (lowerMessage.includes('weight') || lowerMessage.includes('load')) {
      return 'Start with bodyweight squats to master the movement pattern. Once you can perform 15-20 perfect bodyweight squats, gradually add weight. Begin with a light barbell or dumbbells and increase load by 5-10% weekly while maintaining perfect form.'
    }
    
    if (lowerMessage.includes('pain') || lowerMessage.includes('hurt')) {
      return 'If you experience pain during squats, stop immediately and assess your form. Common causes include poor mobility, incorrect technique, or underlying issues. Consider consulting a qualified trainer or physical therapist. Pain is different from muscle fatigue - never push through actual pain.'
    }
    
    if (lowerMessage.includes('breathing') || lowerMessage.includes('breath')) {
      return 'Proper breathing is crucial for squats. Take a deep breath at the top, hold it during the descent and ascent (Valsalva maneuver), then exhale at the top. This creates intra-abdominal pressure for core stability and spine protection.'
    }
    
    if (lowerMessage.includes('warm up') || lowerMessage.includes('warmup')) {
      return 'Always warm up before squatting! Start with 5-10 minutes of light cardio, then do dynamic stretches like leg swings, hip circles, and bodyweight squats. Include mobility work for ankles, hips, and thoracic spine. A proper warm-up reduces injury risk and improves performance.'
    }
    
    // Default response
    return 'That\'s a great question about squat technique! Here are some key points to remember:\n\n• Keep your core engaged throughout the movement\n• Maintain proper knee alignment with your toes\n• Focus on controlled movement, both down and up\n• Ensure adequate depth while maintaining good form\n• Practice regularly with proper progression\n\nWould you like me to elaborate on any specific aspect of squat technique?'
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    if (!input.trim() || isLoading) return

    const userMessage: Message = {
      id: Date.now().toString(),
      role: 'user',
      content: input.trim(),
      timestamp: new Date()
    }

    setMessages(prev => [...prev, userMessage])
    setInput('')
    setIsLoading(true)

    try {
      // Simulate API delay
      await new Promise(resolve => setTimeout(resolve, 1000))
      
      const aiResponse = await generateAIResponse(userMessage.content)
      
      const assistantMessage: Message = {
        id: (Date.now() + 1).toString(),
        role: 'assistant',
        content: aiResponse,
        timestamp: new Date()
      }

      setMessages(prev => [...prev, assistantMessage])
    } catch (error) {
      console.error('Error generating response:', error)
      const errorMessage: Message = {
        id: (Date.now() + 1).toString(),
        role: 'assistant',
        content: 'I apologize, but I\'m having trouble responding right now. Please try asking your question again.',
        timestamp: new Date()
      }
      setMessages(prev => [...prev, errorMessage])
    } finally {
      setIsLoading(false)
    }
  }

  return (
    <Card className="w-full max-w-md bg-gray-800 border-gray-700 text-white">
      <CardHeader className="pb-3">
        <CardTitle className="flex items-center space-x-2">
          <div className="w-8 h-8 bg-yellow-500 rounded-full flex items-center justify-center">
            <span className="text-black font-bold text-sm">AI</span>
          </div>
          <span>Squat Coach</span>
        </CardTitle>
      </CardHeader>
      <CardContent className="space-y-4">
        <ScrollArea ref={scrollAreaRef} className="h-80 w-full border border-gray-600 rounded p-3">
          <div className="space-y-4">
            {messages.map((message) => (
              <div key={message.id} className={`flex ${message.role === 'user' ? 'justify-end' : 'justify-start'}`}>
                <div className={`max-w-[80%] p-3 rounded-lg ${
                  message.role === 'user' 
                    ? 'bg-yellow-500 text-black' 
                    : 'bg-gray-700 text-white'
                }`}>
                  <p className="text-sm whitespace-pre-line">{message.content}</p>
                  <p className="text-xs opacity-70 mt-1">
                    {message.timestamp.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' })}
                  </p>
                </div>
              </div>
            ))}
            {isLoading && (
              <div className="flex justify-start">
                <div className="bg-gray-700 text-white p-3 rounded-lg">
                  <div className="flex space-x-1">
                    <div className="w-2 h-2 bg-gray-400 rounded-full animate-bounce"></div>
                    <div className="w-2 h-2 bg-gray-400 rounded-full animate-bounce" style={{ animationDelay: '0.1s' }}></div>
                    <div className="w-2 h-2 bg-gray-400 rounded-full animate-bounce" style={{ animationDelay: '0.2s' }}></div>
                  </div>
                </div>
              </div>
            )}
          </div>
        </ScrollArea>
        
        <form onSubmit={handleSubmit} className="flex space-x-2">
          <Input
            value={input}
            onChange={(e) => setInput(e.target.value)}
            placeholder="Ask about squat technique..."
            className="bg-gray-700 border-gray-600 text-white flex-1"
            disabled={isLoading}
          />
          <Button 
            type="submit" 
            className="bg-yellow-500 hover:bg-yellow-600 text-black"
            disabled={isLoading || !input.trim()}
          >
            Send
          </Button>
        </form>
        
        <div className="text-xs text-gray-400 text-center">
          💡 Try asking about: form, depth, knees, breathing, warm-up
        </div>
      </CardContent>
    </Card>
  )
}
